body {
  font-family: "Roboto", "Arial", sans-serif;
  // font-size: $text-mobile-font;
  // line-height: $text-mobile-line;
  color: $main-text-color;
  font-weight: 300;
  background-color: $main-bg-color;
  min-width: $width-mobile;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
}

.hr {
  width: 100%;
  height: 1px;
  background-color: $main-border-color;
  margin-block-end: $padding-mobile;
}

.hr--shadow {
  box-shadow: 0 5px 10px 2px $main-border-color;
}

.or {
  padding-block-end: 10px;
  position: relative;
}

.or::before,
.or::after {
  position: absolute;
  content: "";
  width: 80px;
  height: 1px;
  background-color: $main-border-color;
  left: 40px;
  top: 10px;
}

.or::after {
  left: 170px;
}

.marginTop-20 {
  margin-block-start: 20px;
}