.list {
  text-align: left;
  overflow: auto;
  margin-inline-start: $padding-mobile;
  margin-inline-end: $padding-mobile;
  padding-inline-start: 20px;
  word-wrap: break-word;
}

.list--error {
  font-size: $sm-text-mobile-font;
  line-height: $sm-text-mobile-line;
}

.list__item {
  margin-block-end: 15px;
}

.list__item--error {
  margin-block-end: 7px;
}

.list__item--info {
  margin-block-end: 7px;
}

.list__item--warning {
  margin-block-end: 7px;
}