.button {
  min-width: 200px;
  height: 40px;
  margin: 0 auto;
  color: $button-common-color;
  font-size: $button-text-mobile-font;
  // line-height: $button-text-mobile-line;
  line-height: 34px;
  font-weight: 400;
  text-decoration: none;
  text-align: center;
  border: 2px solid $button-common-color;
  border-radius: $button-border-radius;
  padding-inline-start: 15px;
  padding-inline-end: 15px;
  display: inline-block;
  margin-block-start: 20px;
  margin-block-end: 30px;
  cursor: pointer;
  box-sizing: border-box;
}

.button--common {
  background-color: $white;
  color: $button-common-color;
  border-color: $button-common-color;

}

.button--common:hover,
.button--common:focus,
.button--common:active {
  color: $button-common-action-color;
  border-color: $button-common-action-color;
}

.button--action {
  color: $white;
  background-color: $button-common-color;
}

.button--action:hover,
.button--action:focus,
.button--action:active {
  background-color: $button-common-action-color;
  border-color: $button-common-action-color;
}

.button--store {
  min-width: 125px;
  margin-block-start: 0;
  margin-block-end: 10px;
  padding: 0;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  color: $button-common-color;
  border-color: $button-common-color;
  transition-property: all;
  transition-duration: 0.2s;
}

.button--store:nth-of-type(2) {
  margin-inline-start: 6px;
}

.button--store:hover,
.button--store:focus,
.button--store:active {
  color: $button-common-action-color;
  border-color: $button-common-action-color;
}

.button--store-google {
  background-image: url("../img/gluu-button-android.svg");
  margin-left: 0;
  font-size: 0;
}

.button--store-google:hover,
.button--store-google:focus,
.button--store-google:active {
  background-image: url("../img/gluu-button-android-action.svg");
}

.button--store-ios {
  background-image: url("../img/gluu-button-ios.svg");
  margin-right: 0;
  font-size: 0;
}

.button--store-ios:hover,
.button--store-ios:focus,
.button--store-ios:active {
  background-image: url("../img/gluu-button-ios-action.svg");
}

.button--wrapper {
  margin: auto;
  display: flex;
  flex-direction: column;

  @media (min-width: $width-mobile-album) {
    margin-inline-start: $padding-mobile;
    margin-inline-end: $padding-mobile;
    flex-direction: row;
    justify-content: space-around;
  }
}

.button--connect {
  padding-block-start: 1px;
}

.button__timer {
  display: inline-block;
  position: relative;
  width: 30px;
  height: 30px;
}

.button__timerAnimation {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  top: 2px;
  left: 0;
  border: 2px solid $main-bg-color;
  border-radius: 50%;
  animation: loader-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: $main-bg-color transparent transparent transparent;
}

// first ring part
.button__timer .button__timerAnimation:nth-child(1) {
  animation-delay: -0.45s;
}

// second ring part
// .button__timer .button__timerAnimation:nth-child(2) {
//   animation-delay: -0.3s;
// }

// third ring part
// .button__timer .button__timerAnimation:nth-child(3) {
//   animation-delay: -0.15s;
// }

@keyframes loader-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
