.form {
  display: flex;
  flex-direction: column;
  padding-inline-start: 15px;
  padding-inline-end: 15px;
  margin: 0;
}

.form__label {
  display: flex;
  flex-direction: column;
  margin-block-start: 5px;
  font-size: $sm-text-mobile-font;
  line-height: $sm-text-mobile-line;
}

.form__label--big {
  font-size: $text-mobile-font;
  line-height: $text-mobile-line;
}

.form__input {
  width: 200px;
  height: 40px;
  border: 1px solid $form-element-color;
  border-radius: $button-border-radius;
  margin-inline-start: auto;
  margin-inline-end: auto;
  margin-block-start: 5px;
  margin-block-end: 10px;
  padding-inline-start: 10px;
  padding-inline-end: 10px;
  font-size: $button-text-mobile-font;
  line-height: $button-text-mobile-line;;
}

.form__input--select {
  appearance: none;
  width: 100%;
  margin-inline-start: 0;
  margin-inline-end: 0;
  background-color: $main-bg-color;
  background-image: url(../img/icon-drop.svg);
  background-repeat: no-repeat;
  background-position: 94% 50%;
  background-size: 15px 15px;
}