.text--link {
  color: $link-text-color;
}

.text--link:hover,
.text--link:focus,
.text--link:active {
  color: $link-text-active-color;
}

.text--light {
  font-weight: lighter;
}

.text--common {
  color: $white;
}

.text--grey {
  color: $text-grey;
}

.text--light-grey {
  color: $form-element-color;
}

.text--action {
  color: $main-action-color;
}

.text--center {
  text-align: center;
  margin: 0 auto;
}

.text--paddingBottom {
  padding-block-end: $padding-mobile;
}

.text--center-tablet {
  text-align: left;

  @media (min-width: $width-tablet) {
    text-align: center;
  }
}

.text--marginTop-0 {
  margin-block-start: 0;
}

.text__sup {
  font-size: 6px;
  line-height: 8px;

